export default {
  head: {
    script: [
      { hid: 'twitter', type: 'text/javascript', async: true, defer: true }
    ]
  },
  props: {
    src: { 
      type: String,
      default: "https://twitter.com/UNMESSCenter?ref_src=twsrc%5Etfw"
    },
    dark: {
      type: Boolean,
      default: false
    },
    dataLinkColor: {
      type: String,
      default: "#2B7BB9"
    }
  },
  mounted() {
    // exists only in client
    let twitterFeed = document.createElement("script");
    twitterFeed.setAttribute("src", "https://platform.twitter.com/widgets.js");
    document.head.appendChild(twitterFeed);
  }
};
