import Event from '@/components/Page/Event'
import SocialMediaNav from "@/components/SocialMedia/SocialMediaNav";
import RecentItem from "@/components/RecentItem";

const GET_EVENTS = `
  query GetEvents {
    allEvents {
      id
      name
      eventName
      subEventName
      description
      location
      time
      date
    }
  }
`;

function graphql(query, variables = {}) {
  return fetch('http://localhost:3000/admin/api', {
    method: 'POST',
    headers: {
      'Content-Type': 'application/json',
    },
    body: JSON.stringify({
      variables,
      query,
    }),
  }).then(function(result) {
    return result.json();
  });
}

export default {

  components: {
    SocialMediaNav,
    Event,
    RecentItem
  }, 

  // Get the event on server side
  async asyncData() {
    const { data } = await graphql(GET_EVENTS);
    return {
      events: data.allEvents,
    };
  },

  methods: {
    async getEvents() {
      const { data } = await graphql(GET_EVENTS);
      this.events = data.allEvents;
    },
  },
  
}
